import React from 'react';
import Card from '../Card/Card.component';
import './Cards.styles.scss';

const Cards = () => {
    return (
        <section className='Cards'>
            <section className='Cards__Container' >
                <Card >Reason Why To Go Solar</Card>
                <Card >We are paying You to go solar</Card>
                <Card >Investment Tax Credit 26%</Card>
                <Card >$0 Down payment <br />Until 2022</Card>
            </section>
            
        </section>
    );
};

export default Cards;